    width: 16px;
    height: 16px;
  
    border: 1px solid hsla(239, 61%, 73%, 0.863);
    border-radius: 100%;
    margin-right: 16px;
  
    position: relative;
}

.SuperRadio_design__3UAwv:hover {
    box-shadow: 2px 1px 2px rgb(170, 65, 184), 0 0 1em rgb(158, 158, 243), 0 0 0.2em rgb(150, 150, 233);
    transition: 0.3s;
}
.SuperRadio_design__3UAwv::before,
.SuperRadio_design__3UAwv::after {
  content: "";
  display: block;

  width: inherit;
  height: inherit;

  border-radius: inherit;

  position: absolute;
  transform: scale(0);
  transform-origin: center center;
}

.SuperRadio_design__3UAwv:before {
  background: hsl(234, 71%, 77%);
  opacity: 0;
  transition: .3s;
}

.SuperRadio_design__3UAwv::after {
  background: hsl(229, 69%, 70%);
  opacity: 1;
  transition: .6s;
}

.SuperRadio_radio__11Bs-:checked+.SuperRadio_design__3UAwv::before {
    opacity: 1;
    transform: scale(.8);
}

.SuperRadio_radio__11Bs-:hover+.SuperRadio_design__3UAwv,
.SuperRadio_radio__11Bs-:focus+.SuperRadio_design__3UAwv {
    border: 1px solidhsl(234, 71%, 77%);
}
  
.SuperRadio_radio__11Bs-:hover+.SuperRadio_design__3UAwv:before,
.SuperRadio_radio__11Bs-:focus+.SuperRadio_design__3UAwv:before {
    background: hsl(234, 71%, 77%);
}
  
.SuperRadio_radio__11Bs-:hover~.SuperRadio_text__2yfla {
    color:hsl(234, 71%, 77%);
}
  
.SuperRadio_radio__11Bs-:focus+.SuperRadio_design__3UAwv::after,
.SuperRadio_radio__11Bs-:active+.SuperRadio_design__3UAwv::after {
